import { useState, useEffect, useRef } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCloud } from '@fortawesome/free-solid-svg-icons';
import { axiosPrivate } from './Api/axios';
import WordCloud from 'react-d3-cloud';
import { scaleOrdinal } from 'd3-scale';
import { schemeCategory10 } from 'd3-scale-chromatic';


const CloudWord = () => {
    const schemeCategory10ScaleOrdinal = scaleOrdinal(schemeCategory10);
    const [clDay, setClDay] = useState('30');
    const [catCl, setCatCl] = useState('telegram_channel');
    const [clData, setClData] = useState([]);
    const [clDayInp, setClDayInp] = useState(30);
    const [loading, setLoading] = useState(false);
    const inputRef = useRef();

    function handleClick() {

        setClDay(clDayInp);
    }

    useEffect(() => {
        inputRef.current.focus();
    }, [])

    function turnToTelegramChannel() {
        setCatCl('telegram_channel');
    }

    function turnToTelegramGroups() {
        setCatCl('telegram_groups');
    }

    function turnToInsta() {
        setCatCl('instagram');
    }

    function turnToTweeter() {
        setCatCl('twitter');
    }

    function turnToPost() {
        setCatCl('news_agency');
    }

    const actFilter = {
        backgroundColor: "rgb(1, 31, 75)",
        color: "white",
        borderLeftColor: "rgb(1, 31, 75)",
    };
    const dActFilter = {
    }

    useEffect(() => {
        let isMounted = true;
        const controller = new AbortController();

        const getCl = async () => {
            try {
                setLoading(true)
                const res = await axiosPrivate.get(`/${catCl}/get_tag_cload?days_ago=${clDay}`,
                    {
                        signal: controller.signal
                    });
                isMounted && setClData(res?.data);
                setLoading(false)
            } catch (err) {
                console.error(err);
            }
        }

        getCl();

        return () => {
            isMounted = false;
            isMounted && controller.abort();
        }
    }, [clDay, catCl])

    return (
        <>
            <h5 className='mt-8 px-4'><span className='inline-block place-content-center align-top text-middle-blue text-xl ml-2'><FontAwesomeIcon icon={faCloud} /></span>ابر موجودیت روزانه</h5>
            <div className='cloudword-container bg-white border-slate-400 rounded-md p-4 mt-4'>
                <nav className='rounded-lg max-w-fit my-2'>
                    <ul className='flex flex-col md:flex-row p-0 m-0'>
                        <li className=' text-white grid place-content-center text-base py-2 px-1 w-28 bg-dark-blue rounded-r-md select-none'>انتخاب بستر</li>
                        <li className='flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 mr-1 hover:bg-dark-blue' style={catCl === "telegram_channel" ? actFilter : dActFilter} onClick={turnToTelegramChannel}>کانال‌های تلگرام</li>
                        <li className='flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 mr-1 hover:bg-dark-blue' style={catCl === "telegram_groups" ? actFilter : dActFilter} onClick={turnToTelegramGroups}>گروه‌های تلگرام</li>
                        <li className='flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 mr-1 hover:bg-dark-blue' style={catCl === "twitter" ? actFilter : dActFilter} onClick={turnToTweeter}>توییتر</li>
                        <li className='flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 mr-1 hover:bg-dark-blue' style={catCl === "instagram" ? actFilter : dActFilter} onClick={turnToInsta}>اینستاگرام</li>
                        <li className='flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 mr-1 hover:bg-dark-blue rounded-l-md' style={catCl === "news_agency" ? actFilter : dActFilter} onClick={turnToPost}>خبرگذاری‌ها</li>
                    </ul>
                </nav>

                <nav className=' bg-light-blue w-1/3 rounded-md mx-auto my-4 border-4 border-blue-950'>
                    <ul className='flex justify-center items-center m-0 p-0'>
                        <li className=' text-blue-950 text-center py-3 px-1 cursor-default'>نمایش در بازه </li>
                        <li className='flex items-center justify-center text-center align-middle'>
                            <input
                                className="search-input select-none inline-block w-10 h-8 text-center my-2 mx-auto rounded-md p-1 border-2 border-blue-950"
                                type="clSearch"
                                name="clSearch"
                                id="clSearch"
                                // placeholder="30"
                                ref={inputRef}
                                value={clDayInp}
                                onChange={(e) => setClDayInp(e.target.value)
                                }
                                onClick={(e) => { setClData([]); setClDayInp('') }}
                            />
                        </li>
                        <li className=' text-blue-950 text-center py-3 px-1 cursor-default'>روزه</li>
                        <li className=' flex justify-center items-center rounded-md text-white bg-blue-950 hover:bg-middle-blue cursor-pointer h-8 text-center py-1 px-2 select-none transition-all duration-200 ease-in' onClick={handleClick}><span>اعمال</span></li>
                    </ul>
                </nav>
                {!clData?.length ? <div className=' w-full h-128 justify-center'>
                    <p className=' text-center text-lg'>برای مشاهده ابر کلمات، تعداد روز مورد نظر را مشخص نمایید.</p>
                    {loading ? <div className=' w-full h-128 flex justify-center'>
                        <p className=' text-center text-lg'>در حال بارگذاری اطلاعات...</p>
                    </div>
                        : ''}
                </div>
                    : <>
                        {!loading ? <WordCloud
                            data={clData}
                            width={500}
                            height={200}
                            font="Vazir"
                            fontStyle="normal"
                            // fontWeight="regular"
                            fontSize={(word) => Math.log(word.value) * 3}
                            spiral="rectangular"
                            rotate={(word) => word.value % 1}
                            padding={1}
                            random={Math.random}
                            fill={(d, i) => schemeCategory10ScaleOrdinal(i)}
                        />
                            : <div className=' w-full h-128 flex justify-center'>
                                <p className=' text-center text-lg'>در حال بارگذاری اطلاعات...</p>
                            </div>}
                    </>
                }
            </div>
        </>
    )
}

export default CloudWord